.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 4em;
  width: 600px;
  padding: 6em 4em;
}

/* Profile */

.profile {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 2em;
}

.profile .image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 160px;
  aspect-ratio: 1/1;
  border-radius: 100%;
  border: 1px solid #D3D3D3;
}

.profile img {
  width: 84%;
  aspect-ratio: 1/1;
  border-radius: 100%;
}

.profile .info {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 1em;
}

.profile .info .name {
  font-size: 1.7em;
  font-weight: 500;
  color: #242424;
}

.profile .info .desc {
  font-size: 1.2em;
  color: #4D4D4D;
}

/* Section */

.section {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 1.6em;

  width: 100%;
}

.section .title {
  font-size: 1.2em;
  color: #646464;
}

.small-card {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  column-gap: 1em;

  height: 4.2em;
  width: 90%;
  border-radius: 0.8em;
  box-shadow: 0 4px 20px #3256b133;
}

.small-card img {
  width: 2em;
}

.small-card .college-name {
  font-size: 1.16em;
  font-weight: 500;
  color: #242424;
}

.socials {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  column-gap: 1.6em;

  height: 3em;
}

.socials .icon {
  color: #293249;
  font-size: 1.6em;
  transition: 0.2s;
  cursor: pointer;
}

.socials .icon:hover {
  transform: scale(1.1);
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  row-gap: 1.6em;
  width: 100%;
}

.buttons a {
  height: 4.2em;
  width: 90%;
  border-radius: 0.8em;
  cursor: pointer;

  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  column-gap: 1em;

  transition: 0.2s;
}

.buttons a:hover {
  transform: scale(1.05);
}

.buttons a .icon {
  font-size: 1em;
}

.buttons a .btn-label {
  font-size: 1.2em;
}

.btn-primary {
  color: white;
  background-color: #293249;
}

.btn-secondary {
  color: #293249;
  background-color: white;
  border: 1px solid #293249;
}

/* Pop Up Message */

.bg-pop {
  position: fixed;
  top: 0;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #00000056;
}

.pop-container {
  background-color: white;
  border-radius: 0.8em;

  padding: 2em;
  display: flex;
  flex-direction: column;
  row-gap: 1.2em;
  justify-content: center;
  align-items: center;
}

.pop-container .pop-title {
  font-size: 1.4em;
  font-weight: 500;
  color: #293249;
}

.pop-container .pop-message {
  font-size: 1em;
  color: #4D4D4D;
}

.pop-container .pop-btn {
  margin-top: 0.8em;
  height: 2.6em;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  border-radius: 0.6em;
  background-color: #293249;
  color: white;
  font-size: 1em;

  cursor: pointer;
  transition: 0.2s;
}

.pop-container .pop-btn:hover {
  transform: scale(1.05);
}

@media (max-width: 600px) {
  .container {
    width: 100vw;
    padding: 4em 2em;
  }
}

@media (max-width: 460px) {
  .container {
    font-size: 0.9em;
    row-gap: 3em;
  }
}